body {
  justify-content: center;
  align-items: center;
  height: 100vh;
  margin: 0;
  display: flex;
}

.stopwatch {
  border: 1vmin dotted #000;
  border-radius: 50%;
  width: 80vmin;
  height: 80vmin;
  position: relative;
}

.stopwatch--speed-up {
  --minute-duration: 10s;
}

.stopwatch__center {
  background-color: #f6a603;
  border-radius: 50%;
  width: 5vmin;
  height: 5vmin;
  position: absolute;
  bottom: 37.5vmin;
  left: 37.5vmin;
}

.stopwatch__minutes-hand {
  animation: rotate calc(var(--minute-duration) * 60) steps(60) infinite;
  background-color: #0700ff;
  width: 3vmin;
  height: 20vmin;
  position: absolute;
  bottom: 40vmin;
  left: 38.5vmin;
}

.stopwatch__seconds-hand {
  animation: rotate var(--minute-duration) linear infinite;
  background-color: #2c8000;
  width: 1.5vmin;
  height: 38vmin;
  position: absolute;
  bottom: 40vmin;
  left: 39.25vmin;
}

@keyframes rotate {
  from {
    transform-origin: bottom;
    transform: rotate(0);
  }

  to {
    transform-origin: bottom;
    transform: rotate(360deg);
  }
}
